body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f9fafb;
  color: #111827;
}
section {
  padding: 60px 20px;
  max-width: 1000px;
  margin: 0 auto;
}
h1, h2 {
  color: #1e3a8a;
}
a {
  text-decoration: none;
  color: #2563eb;
}
html {
  scroll-behavior: smooth;
}